$base-font-family: Pretendard, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol" !default;
$text-color:       #333;

@import "minima";

.post-content { line-height: 1.75; }

.post-content hr { background: url("/assets/pagebreak.png") no-repeat center center; background-size: contain; height: 1.5rem; border: none; margin: 2rem 0; opacity: .5; }
.post-content hr + h1 { margin-top: 0; }

.post-content h1,
.post-content h2,
.post-content h3,
.post-content h4,
.post-content h5 { border-bottom: 1px solid #e8e8e8; box-shadow: 0 1px 0 0 #fff; }

.post-content svg { max-width: 100%; }

.PageNavigation {
  font-size: 14px;
  display: block;
  width: auto;
  overflow: hidden;
}

.PageNavigation a {
  display: block;
  width: 50%;
  float: left;
  margin: 1em 0;
}

.PageNavigation .next {
  text-align: right;
  width: 100%;
}

.PageNavigation a + .next {
  width: 50%;
}

pre { padding: .75rem 1rem; }

pre.highlight {
  line-height: 1.15;
  
  code {
    font-size: .75rem;
  }
}

pre, code {
  background-color: #fff0f0;
}

.page-content {
  .home {
    h1 {
      margin-top: 2rem;
      padding-top: 2rem;
      border-top: 1px solid #e8e8e8;
      
      &:first-child {
        margin-top: 0;
        padding-top: 0;
        border-top-style: none;
      }
    }
  }
}

.post-content {
  margin-bottom: $spacing-unit;

  h2 {
    @include relative-font-size(2.75);

    margin-top: 4rem;
    color: #111;

    @include media-query($on-laptop) {
      @include relative-font-size(2.5);

      margin-top: 3rem;
    }
   
    &:first-child {
      margin-top: 0;
    }
  }

  h3 {
    @include relative-font-size(2);

    margin-top: 2rem;

    @include media-query($on-laptop) {
      @include relative-font-size(1.75);
    }
  }

  h4 {
    @include relative-font-size(1.625);

    @include media-query($on-laptop) {
      @include relative-font-size(1.375);
    }
  }

  h5 {
    @include relative-font-size(1.25);

    @include media-query($on-laptop) {
      @include relative-font-size(1.125);
    }
  }
  
  table th,
  table td { padding: .125rem .75rem; }
  
  img.image-center {
    display: block;
    margin: 0 auto;
  }
  .image-caption {
    margin-top: .5rem;
    display: block;
    text-align: center;
  }
  .image-caption:before {
    content: '<';
  }
  .image-caption:after {
    content: '>';
  }
}

.graphviz {
  max-width: 100%;
  overflow: auto;
  
  &.image-center svg {
    display: block;
    margin: 0 auto;
  }
}

.post-content .graphviz svg {
  max-width: 200%;
}

.post-content .graphviz.max-w-100 svg {
  max-width: 100%; 
}

#disqus_thread {
  margin-top: 2rem;
}

.site-title {
  position: relative;

  @media (max-width: 900px) {
    padding-left: 60px;
  }
}

.site-header {
  box-sizing: border-box;;
  border-top-color: #1756a9;
  background: #fff;
  transition: .25s;
}

.scrolled {
  .site-header {
    position: fixed;
    top: -5px;
    width: 100%;
    z-index: 1000;
    background: rgba(255, 255, 255, .90);
    //border-top: 0;
    box-shadow: 0 0 10px rgba(0, 0, 0, .1);
    backdrop-filter: blur(2px);
  }

  main {
    margin-top: 57px;
  }
}

.site-title:before {
  content: '';
  display: block;
  width: 48px; 
  height: 48px;
  background-image: url('/assets/top_icon.png');
  position: absolute;
  left: -64px;
  top: 4px;
  
  @media (max-width: 900px) {
    left: 0;
  }
}

.footer-heading {
  position: relative;
}

.footer-heading:before {
  content: '';
  display: block;
  width: 32px; 
  height: 32px;
  background-image: url('/assets/bottom_icon.png');
  background-repeat: no-repeat;
  position: absolute;
  left: -48px;
  top: -2px;
  
  @media (max-width: 900px) {
    display: none;
  }
}

#back-to-top {
  position: fixed;
  bottom: 3.5rem;
  right: 4rem;
  opacity: 0;

  background: #999;
  
  border: 1px solid #777;
  
  transition: .25s;
  
  &:hover {
    background: #666;
  }

  @media (max-width: 900px) {
    bottom: 3rem;
    right: 1rem;
  }
  
  span {
    display: block;
    position: relative;;
    width: 56px;
    height: 56px;
    overflow: hidden;
    text-indent: -1000px;
    
    &:before {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) rotate(45deg);
      
      border: 2px solid #fff;
      border-right-style: none;
      border-bottom-style: none;
      content: '';
      display: block;
      width: 8px;
      height: 8px;      
    }
  }
}

.scrolled #back-to-top {
  opacity: 1;
}

.pagination {
  margin-top: 3rem;
  margin-bottom: 1rem;
  text-align: center;
  
  > a, 
  > em,
  > span {
    margin-left: .5rem;
    margin-right: .5rem;
  }
  
  > em {
    font-weight: bold;
  }
}

.table-of-content {
  display: inline-block;
  border: 1px solid #ccc;
  
  > header {
    font-size: 1.125rem;
    font-weight: 400;
    padding: 0 1rem;
    border-bottom: 1px solid #ddd;
  }
  
  #toc {
    padding: .5rem 1.5rem 0 .5rem;
    margin-bottom: .5rem;
  }
}

.toc-minimap {
  position: fixed;
  right: 1.5rem;
  top: 150px;
  opacity: 0;

  @media (max-width: 900px) {
    display: none;
  }

  &.active {
    font-size: .875rem;
    background: rgba(255, 255, 255, .95);
    border: 1px solid #e9e9e9;
    border-radius: 8px;
    padding: 1rem 1.5rem 0 1rem;
    right: 1rem;
    box-shadow: 0 0 10px rgba(0, 0, 0, .1);
    transform: translate(100px, 0);
    
    ul {
      margin-left: 20px;
      
      li.active > a {
        font-weight: bold;
        color: #333;
      }
    }
    
    &.animated {
      opacity: 1;
      transition: .25s;
      transform: translate(0, 0);
    }
  }
  
  &.collapsed {
    top: 200px;
    opacity: 1;

    ul {
      list-style: none;
      padding: 0;
      margin-top: 0;
      margin-bottom: 0;
      margin-right: 0;
      margin-left: 4px;

      li {
        &:before {
          display: block;
          content: '';
          width: 100%;
          min-width: 8px;
          background: #333;
          opacity: .25;
          height: 2px;
          margin-bottom: 8px;
          transition: .25s;
        }

        &.active {
          &:before {
            background: #1756a9;
            opacity: 1;
          }
        }
      }

      a {
        display: none;
      }
    }
  }
}